// pages/index.js
import {Component} from 'react'
import Layout from '../components/MyLayout.js'
import HeaderCategory from '../components/HeaderCategory.js'

class Index extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  componentDidMount () {
    console.log("component did mount");
    var mymap = L.map('mapid', {dragging: false, scrollWheelZoom: false}).setView([51.505, -0.09], 13);
    L.tileLayer('https://api.tiles.mapbox.com/v4/{id}/{z}/{x}/{y}.png?access_token={accessToken}', {
      attribution: 'Map data &copy; <a href="https://www.openstreetmap.org/">OpenStreetMap</a> contributors, <a href="https://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>, Imagery Â© <a href="https://www.mapbox.com/">Mapbox</a>',
      maxZoom: 18,
      id: 'mapbox.streets',
      accessToken: 'pk.eyJ1IjoiYXZpc3RpIiwiYSI6ImNqbGlkYjF0NTAwMWkza3J6bWg4OGU1ajMifQ.CaYGpEN8sjY8LKaGSr4U_A'
    }).addTo(mymap);

  }

  render() {
    return (
      <Layout>
        <HeaderCategory />
        <div className="grid">
          <div className="col-1-1 floatLeft">
            <div id="mapid"></div>
            <p>This is some frontpage content</p>
          </div>
        </div>
        <div className="grid">
          <div className="col-1-1 floatLeft">
            <div id="mapid"></div>
            <p>This is some frontpage content</p>
          </div>
        </div>
      </Layout>
    )
  }
}

export default Index